ul.listing.posts {
  margin-top: 2rem; }
  ul.listing.posts > li {
    padding: 2rem 0;
    text-align: center; }
    ul.listing.posts > li h3,
    ul.listing.posts > li span.date {
      display: block;
      padding: 1rem; }
    ul.listing.posts > li > div {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      -ms-flex-direction: row;
      flex-direction: row;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -ms-flex-pack: distribute;
      justify-content: space-around;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap; }
      ul.listing.posts > li > div img {
        max-width: 45%;
        height: auto;
        margin: 1rem;
        border: 2px solid rgba(0, 0, 0, 0.5); }
      ul.listing.posts > li > div div {
        max-width: 40%;
        margin: 1rem 1rem 1rem 2rem;
        text-align: left; }
        ul.listing.posts > li > div div p {
          padding-bottom: 1rem; }
  ul.listing.posts > li:after {
    content: "";
    position: absolute;
    left: calc(50vw - 15rem);
    height: 1px;
    width: 30rem;
    max-width: 106vw;
    margin-top: 1rem;
    background-color: black; }

ul.listing.posts:before {
  content: "";
  position: absolute;
  left: calc(50vw - 15rem);
  height: 1px;
  width: 30rem;
  max-width: 106vw;
  margin-top: -1rem;
  background-color: black; }

@media screen and (max-width: 640px) {
  ul.listing.posts > li > div img,
  ul.listing.posts > li > div div {
    max-width: 80%; }
  ul.listing.posts > li:after {
    width: 100vw;
    left: 0; }
  ul.listing.posts > li:last-child:after {
    width: 0; }
  ul.listing.posts:before {
    width: 100vw;
    left: 0; } }
